<template>
    <div class="main-wrapper">
         <layout-header2></layout-header2>
         <breadcrumb5></breadcrumb5>
         <!-- Page Content -->
			<div class="content">
				<div class="container-fluid">

					<div class="row">
						<div class="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
                            <sidebar></sidebar>
						</div>

							<div class="col-md-7 col-lg-8 col-xl-9">
							<div class="doc-review review-listing">
							
								<!-- Review Listing -->
								<ul class="comments-list">
								
									<!-- Comment List -->
									<li>
										<div class="comment">
											<img class="avatar rounded-circle" alt="User Image" src="@/assets/img/patients/patient.jpg">
											<div class="comment-body">
												<div class="meta-data">
													<span class="comment-author">Nicolas Flowers</span>
													<span class="comment-date">Reviewed 2 Days ago</span>
													<div class="review-count rating">
														<i class="fas fa-star filled"></i>
														<i class="fas fa-star filled"></i>
														<i class="fas fa-star filled"></i>
														<i class="fas fa-star filled"></i>
														<i class="fas fa-star"></i>
													</div>
												</div>
												<p class="recommended"><i class="far fa-thumbs-up"></i> I recommend the doctor</p>
												<p class="comment-content">
													Lorem ipsum dolor sit amet, consectetur adipisicing elit,
													sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
													Ut enim ad minim veniam, quis nostrud exercitation.
													Curabitur non nulla sit amet nisl tempus
												</p>
												
											</div>
										</div>
										
										
										
									</li>
									<!-- /Comment List -->
									
								
									
								</ul>
								<!-- /Comment List -->
								
							</div>
						</div>
					</div>
				</div>
			</div>		
			<!-- /Page Content -->
        <layout-footer></layout-footer>
     </div>
</template>

<script>
import patients from '../../assets/json/doctor/mypatients.json'
const images = require.context('@/assets/img/patients', false, /\.png$|\.jpg$/)
export default {
    data() {
        return {
            patients: patients
        }
    },
     methods:{
        loadImg(imgPath) {
            return images('./' + imgPath)
        },
    }
}
</script>


